module.exports =
  props:
    id: String
    name: String
    value:
      required: true
    checked:
      required: true
      twoWay: true
    disabled: Boolean
  computed:
    isChecked: -> @checked is @value
  ready: ->
    componentHandler.upgradeElements @$el


